@import url(https://fonts.googleapis.com/css2?family=Miltonian+Tattoo&family=Open+Sans:wght@400;700&display=swap);
html,body,#root{box-sizing:border-box;font-size:16px;min-height:100%;margin:0;font-family:'Open Sans', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#333}*,*:before,*:after{box-sizing:inherit}h1,h2,h3,h4,h5,h6{font-family:'Miltonian Tattoo', cursive}.container{display:flex;flex-direction:column;padding:1rem;margin:0 auto;max-width:1100px}h1{display:flex;justify-content:space-between;align-items:center;text-shadow:2px 2px 2px rgba(42,42,42,0.25)}@media only screen and (min-width: 641px){h1{justify-content:flex-start}h1 img{margin-right:2rem}}.mainLogo{width:100px;height:auto}@media only screen and (min-width: 641px){.mainLogo{width:150px}}.uploadForm form{display:flex;justify-content:center;align-items:center;margin-top:2rem;width:100%}.uploadForm form label{background-color:#ff884d;font-weight:700;color:#fff;border-radius:4px;border:1px solid #cd4400;display:inline-block;padding:1rem;cursor:pointer;width:100%;text-align:center}@media only screen and (min-width: 641px){.uploadForm form label{width:50%}}.uploadForm form input[type='file']{display:none}.ticketRow{padding:1rem}.ticketRow:nth-child(even){background-color:#f0f0f0}

